export default {
  namespaced: true,

  state: {
    current: null
  },

  getters: {
    current: state => state.current
  },

  mutations: {
    SET_CURRENT_EVENT: (state, current) => {
      state.current = current
    }
  },

  actions: {
    setCurrentEvent: ({ commit }, event) => {
      commit('SET_CURRENT_EVENT', event)
    }
  }
}
