export default {
  namespaced: true,

  state: {
    pages: []
  },

  getters: {
    pages: state => state.pages
  },

  mutations: {
    SET_PAGES: (state, pages) => {
      state.pages = pages
    }
  },

  actions: {
    setPages: ({ commit }, pages) => {
      commit('SET_PAGES', pages)
    }
  }
}
