<template>
  <div class="NDT-Navbar">
    <router-link :to="{ name: 'home' }" class="NDT-Navbar__Brand">
      <img
        :src="logo"
        alt="NDT-LAN"
      >
    </router-link>

    <div class="NDT-Navbar__Event">
      <template v-if="event">
        <h3>{{ event.name }}</h3>
        <h5>{{ eventDate }}</h5>
      </template>
    </div>

    <b-navbar toggleable="md">
      <b-collapse is-nav id="nav_collapse" @show="menuToggled = true" @hide="menuToggled = false">

        <b-navbar-nav class="NDT-Navbar__Items">
          <b-nav-item
            v-for="page in sortedPages"
            :key="page.id"
            :to="{ name: 'page', params: { slug: page.slug } }"
            class="NDT-Navbar__Item"
          >
            {{ page.name }}
          </b-nav-item>
        </b-navbar-nav>

      </b-collapse>

      <b-navbar-toggle target="nav_collapse" class="NDT-Navbar__Toggle">
        <a href="#">
          <icon :icon="menuToggled ? 'chevron-down' : 'chevron-right'" />
          <span class="NDT-Navbar__ToggleLabel">
            {{ menuToggled ? 'Lukk' : 'Meny' }}
          </span>
        </a>
      </b-navbar-toggle>
    </b-navbar>
  </div>
</template>

<script>
  import moment from 'moment'
  import { mapActions, mapGetters } from 'vuex'
  import logo from '@/assets/images/logo.svg'

  export default {
    name: 'NDTNavbar',

    props: {
      slug: {
        type: String,
        default: null
      }
    },

    data: _ => ({
      logo,
      menuToggled: false
    }),

    computed: {
      ...mapGetters({
        pages: 'pages/pages',
        event: 'events/current'
      }),

      sortedPages () {
        return this.pages
          .filter(_ => true)
          .sort((a, b) => a.name.localeCompare(b.name))
      },

      eventDate () {
        if (this.event) {
          const start = this.event.start
            ? moment(this.event.start).format('DD.MM')
            : 'TBA'
          const end = this.event.end
            ? moment(this.event.end).format('DD.MM')
            : 'TBA'

          return start === end ? 'TBA' : `${start} - ${end}`
        }
      }
    },

    methods: {
      ...mapActions({
        setPages: 'pages/setPages',
        setCurrentEvent: 'events/setCurrentEvent'
      }),

      async getPages () {
        const pages = await this.$api.get('pages')

        this.setPages(pages)
      },

      async getCurrentEvent () {
        const current = await this.$api.get('events/current')

        this.setCurrentEvent(current)
      }
    },

    mounted () {
      this.getCurrentEvent()
      this.getPages()
    }
  }
</script>

