export default {
  namespaced: true,

  state: {
    articles: []
  },

  getters: {
    articles: state => state.articles
  },

  mutations: {
    SET_ARTICLES: (state, articles) => {
      state.articles = articles
    }
  },

  actions: {
    setArticles: ({ commit }, articles) => {
      commit('SET_ARTICLES', articles)
    }
  }
}
