class ApiClient {
  /**
   * @param {String} root Root URL for API endpoint
   * @param {Object} options Request options for fetch
   */
  constructor (root = '/', options = { redirect: 'follow' }) {
    this.$root = root
    this.options = options
    this.headers = {}
  }

  /**
   * Builds a URL by joining path components
   * @param {...String} paths
   * @return {String}
   */
  join (...paths) {
    return paths.map(path => path.trim()
      .replace(/\/$/, '')
      .replace(/^\//, '')
    ).join('/')
  }

  /**
   * @type {Object}
   */
  get config () {
    return { ...this.options }
  }

  /**
   * Generates request URL by joining $root + path
   * @param {String} path
   * @return {String}
   */
  makeURL (path) {
    return this.join(this.$root, path)
  }

  /**
   * Creates Request using fetch
   * @async
   * @param {String} [url = '/'] relative to $root
   * @param {String} [method = 'get']
   * @param {String} [body]
   * @return {Promise}
   */
  async makeRequest (url = '/', method = 'get', body, headers = {}) {
    url = this.makeURL(url)

    headers = { ...this.headers, ...headers }

    const request = await fetch(url, {
      headers,
      ...this.config,
      method: method.toUpperCase(),
      body: JSON.stringify(body)
    })

    return request.json()
  }

  /**
   * Request using GET method
   * @async
   * @param {String} [path = '/']
   * @return {Promise}
   */
  async get (path = '/') {
    return this.makeRequest(path)
  }

  /**
   * Request using POST method
   * @async
   * @param {String} [path = '/']
   * @param {*} [data]
   * @return {Promise}
   */
  async post (path = '/', data) {
    return this.makeRequest(path, 'post', data, {
      'Content-Type': 'application/json'
    })
  }

  /**
   * Request using PUT method
   * @async
   * @param {String} [path = '/']
   * @param {*} [data]
   * @return {Promise}
   */
  async put (path = '/', data) {
    return this.makeRequest(path, 'put', data, {
      'Content-Type': 'application/json'
    })
  }

  /**
   * Request using DELETE method
   * @async
   * @param {String} [path = '/']
   * @param {*} [data]
   * @return {Promise}
   */
  async delete (path = '/', data) {
    return this.makeRequest(path, 'delete', data)
  }
}

export default ApiClient
