export default function redirect (location, callback) {
  if (location.search) {
    const query = {}

    location.search.slice(1)
      .split('&')
      .forEach(v => {
        const a = v.split('=');
        query[a[0]] = a.slice(1)
          .join('=')
          .replace(/~and~/g, '&')
      })

    if (query.p !== undefined) {
      const url = decodeURIComponent(
        location.pathname.slice(0, -1) + (query.p || '') +
        (query.q ? ('?' + query.q) : '') +
        location.hash
      )

      callback(url)
    }
  }
}
